import hljs from "highlight.js";
import FroalaEditor from "froala-editor";

const EMPTY_SELECTION_TEXT =
  "Select text to display the content in a code block.";

const POPUP_STYLE =
  "font-size: 14px; padding: 20px; max-width: 200px; word-break: break-word";

FroalaEditor.POPUP_TEMPLATES["codeSnippet.popup"] = "[_CUSTOM_LAYER_]";

FroalaEditor.PLUGINS.codeSnippet = function (editor) {
  const apply = () => {
    const blocks = editor.selection.blocks();
    const text = blocks
      .map((block) => `<div>${block.innerHTML}</div>`)
      .join("");
    editor.html.insert(
      `<pre class="before-hl"><code class="hljs">${text}</code></pre><br>`
    );

    const codeBlock = editor.el;
    codeBlock.querySelectorAll(".before-hl code br").forEach((element) => {
      element.replaceWith(document.createTextNode("\n"));
    });

    codeBlock.querySelectorAll("pre.before-hl code div").forEach((element) => {
      hljs.highlightElement(element);
    });

    codeBlock.querySelectorAll("pre.before-hl").forEach((block) => {
      block.classList.remove("before-hl");
    });
  };
  const initPopup = () => {
    const template = {
      custom_layer: `<div style="${POPUP_STYLE}">${EMPTY_SELECTION_TEXT}</div>`
    };
    return editor.popups.create("codeSnippet.popup", template);
  };
  const showPopup = () => {
    let $popup = editor.popups.get("codeSnippet.popup");
    if (!$popup) initPopup();
    editor.popups.setContainer("codeSnippet.popup", editor.$tb);
    editor.popups.refresh("codeSnippet.popup");
    const $btn = editor.$tb.find('.fr-command[data-cmd="codeSnippet"]');
    const left = $btn.offset().left - 10;
    const top =
      $btn.offset().top +
      (editor.opts.toolbarBottom ? 10 : $btn.outerHeight() - 10);
    editor.popups.show("codeSnippet.popup", left, top, $btn.outerHeight());
  };
  return {
    apply: apply,
    showPopup: showPopup
  };
};

FroalaEditor.DefineIconTemplate(
  "cyware-design",
  '<i class="cy-flex-center cy-text-f18 cyicon cyicon-code-snippet"></i>'
);

FroalaEditor.DefineIcon("codeSnippetIcon", {
  NAME: "codeSnippetIcon",
  template: "cyware-design"
});

FroalaEditor.RegisterCommand("codeSnippet", {
  title: "Code Snippet",
  icon: "codeSnippetIcon",
  undo: true,
  focus: true,
  refreshAfterCallback: true,
  callback: function () {
    const isSelected = !this.selection.isCollapsed();
    isSelected ? this.codeSnippet.apply() : this.codeSnippet.showPopup();
  }
});
